import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';

import './nav.scss';

const items = [
  {
    id: 0,
    title: "音乐",
    path: "/main/music"
  },
  {
    id: 1,
    title: "电影",
    path: "/main/movie"
  },
  {
    id: 2,
    title: "图片",
    path: "/main/pic"
  },

  {
    id: 3,
    title: "文章",
    path: "/main/article"
  },
  {
    id: 4,
    title: "关于",
    path: "/main/about"
  },
  {
    id: 5,
    title: "留言",
    path: "/main/comment"
  }];

class Nav extends Component {
  constructor(props) {
    super(props);
    this.state = {
      navIndex: 0,
    }
  }

  componentWillMount() {}

  handleClick(index) {
    this.setState({
      navIndex: index,
    });
  }

  render() {
    let navFigure = items.map((element, index) => {
      return (
        <li
            key={ index }
            className="col-md-1 col-xs-3"
            onClick={ this.handleClick.bind(this, index) }>
          <NavLink
                   to={ element.path }
                   activeClassName="selected">
            { element.title }
          </NavLink>
        </li>);
    });


    return (
      <div className="Nav container-fluid">
        <ul className="row">
          <li className="col-md-2 col-xs-12 text-center">
            <NavLink to="/">
              <img
                   src={ require('../imgs/blogease.png') }
                   alt="logo"></img>
            </NavLink>
          </li>
          <li className="col-md-4"></li>
          { navFigure }
        </ul>
      </div>
      );
  }
}

export default Nav;


