import React, { Component } from 'react';
import PageButton from './PageButton';

import { listData } from './listData';


class Pagination extends Component {
  constructor(props) {
    super(props);
    this.state = {
      options: {
        data: listData, //总数据
        indexData: [], //显示页面的数据
        currentPage: 1, //当前页码
        pageSize: 8, //每页显示的条数
        totalPage: 0, //需要分几页,通过计算获得
        goIndex: 0,
      }
    }
  }


  componentWillMount() {

    let totalPage = Math.ceil(this.state.options.data.length / this.state.options.pageSize);
    let options = this.state.options;
    options.totalPage = totalPage;
    this.setState({
      options
    });

    this.pageChange(this.state.options.goIndex);

  }

  setPage(index) {

    let pageSize = this.state.options.pageSize;
    let options = this.state.options;
    options.goIndex = index;
    options.currentPage = index + 1;
    options.indexData = options.data.slice(index * pageSize, (index + 1) * pageSize);
    this.setState({
      options
    });

  }

  pageChange(index) {
    this.setPage(index);
  }



  render() {
    let data = this.state.options.indexData;
    let dataFigure = data.map((element, index) => {
      return (
        <tr key={ index }>
          <td>
            { element.idd }
          </td>
          <td>
            { element.title }
          </td>
          <td>
            { element.contents }
          </td>
        </tr>
        );
    });

    return (
      <div
           className="Pagination"
           style={ { textAlign: 'center', marginBottom: '30px' } }>
        <fieldset>
          <legend style={ { color: "#01AAED" } }>
            数据分页
          </legend>
        </fieldset>
        <div>
          <table className="table">
            <tbody>
              { dataFigure }
            </tbody>
          </table>
        </div>
        <PageButton
                    { ...this.state }
                    pageChange={ this.pageChange.bind(this) } />
      </div>
      );
  }
}

export default Pagination;


