import React , { Component } from 'react';
import './about.scss';
class About extends Component {
  constructor(props) {
    super(props);

    this.state = {
      isShow: false,
    }
  }

  handleClick() {
    this.setState({
      isShow: true,
    });
  }

  hideBack() {
    this.setState({
      isShow: false,
    });
  }

  render() {
    return (
      <div className="About ">
        <div className="message-box container-fluid">
          <div className="hr hr-up"></div>
          <h2>白延辉</h2>
          <div className="hr hr-down"></div>
          <button
                  className="btn btn-success"
                  onClick={ this.handleClick.bind(this) }>
            赞助我
          </button>
        </div>
        <div className={ `sponsor ${this.state.isShow ? "show" : ""}` }>
          <h3 className="sponsor-title">扫二维码赞助</h3>
          <div className="sponsor-body row">
            <div className="col-md-6 col-xs-6 left">
              <div className="text-center">
                <h4>微信</h4>
                <img
                     src={ require("../imgs/left.jpg") }
                     alt=""
                     width="160px"
                     height="160px" />
              </div>
            </div>
            <div className="col-md-6 col-xs-6 right">
              <div className="text-center">
                <div>
                  <h4>支付宝</h4>
                  <img
                       src={ require("../imgs/right.jpg") }
                       alt=""
                       width="160px"
                       height="160px" />
                </div>
              </div>
            </div>
          </div>
        </div>
        <div
             className={ `mask ${this.state.isShow ===false ? "hide" : ""}` }
             onClick={ this.hideBack.bind(this) }></div>
      </div>
      );
  }
}

export default About;


