import React, { Component } from 'react';
import { MOVIE_LIST } from './movielist';
import { Link } from 'react-router-dom';
import LazyLoad from 'react-lazyload';
import './showmovie.scss';
class ShowMovie extends Component {

  render() {

    let list = MOVIE_LIST.filter((element, index) => {
      return element.director === "诺兰"
    });

    let movieFigure = list.map((element, index) => {
      return (
        <div
             className="col-md-3 col-sm-4"
             key={ index }>
          <div className="img-sec text-center">
            <Link to={ `/movie/${element.id}` }>
              <LazyLoad height={ 280 }>
                <img
                     src={ element.post }
                     alt={ element.id }
                     width="200px"
                     height="280px" />
              </LazyLoad>
            </Link>
            <p>
              <Link to={ `/movie/${element.id}` }>
                { element.name }
              </Link>
            </p>
          </div>
        </div>
        );
    });

    let list2 = MOVIE_LIST.filter((element, index) => {
      return element.director === "史蒂文-斯皮尔伯格"
    });

    let secondMovieFigure = list2.map((element, index) => {
      return (
        <div
             className="col-md-3 col-sm-4"
             key={ index }>
          <div className="img-sec text-center">
            <Link to={ `/movie/${element.id}` }>
              <img
                   src={ element.post }
                   alt={ element.id }
                   width="200px"
                   height="280px" />
            </Link>
            <p>
              <Link to={ `/movie/${element.id}` }>
                { element.name }
              </Link>
            </p>
          </div>
        </div>
        );
    });


    return (

      <div className="ShowMovie">
        <fieldset>
          <legend>
            诺兰电影
          </legend>
        </fieldset>
        <div className="container-fluid">
          { movieFigure }
        </div>
        <fieldset>
          <legend>
            斯皮尔伯格电影
          </legend>
        </fieldset>
        <div className="container-fluid">
          { secondMovieFigure }
        </div>
      </div>
      );
  }
}

export default ShowMovie;


