import React , { Component } from 'react';
import Lazyload from './article/Lazyload';
import Gallery from './article/Gallery';
import './article.scss';
class Article extends Component {
  render() {
    return (
      <div className="Article container">
        <fieldset>
          <legend>
            我的文章
          </legend>
        </fieldset>
        <Lazyload />
        <Gallery />
      </div>


    )
  }
  ;
}

export default Article;


