import React , { Component } from 'react';
import { MUSIC_LIST } from '../data/musiclist';
import Pubsub from 'pubsub-js';
import $ from 'jquery';
import 'jplayer';
import Player from './music/Player';
import Musiclist from './music/musiclist';
import './music.scss';


class Music extends Component {
  constructor(props) {
    super(props);
    this.state = {
      musiclist: MUSIC_LIST,
      currentMusicItem: MUSIC_LIST[0],
      isPlay: null,
    };
  }

  playMusic(musicItem) {
    $('#player').jPlayer('setMedia', {
      mp3: musicItem.file
    }).jPlayer('play');
    this.setState({
      currentMusicItem: musicItem,
    })
  }

  playNext(type = 'next') {
    let index = this.findMusicIndex(this.state.currentMusicItem);
    let newIndex = null;
    let length = this.state.musiclist.length;
    if (type === 'next') {
      newIndex = (index + 1) % length;
    } else {
      newIndex = (index - 1 + length) % length;
    }
    this.playMusic(this.state.musiclist[newIndex])
  }


  findMusicIndex(musicItem) {
    return this.state.musiclist.indexOf(musicItem);
  }



  componentDidMount() {

    $('#player').jPlayer({
      supplied: 'mp3',
      wmode: 'window'
    });

    this.playMusic(this.state.currentMusicItem);

    $('#player').bind($.jPlayer.event.ended, (e) => {
      this.playNext();
    });

    Pubsub.subscribe('DELETE_MUSIC', (msg, musicItem) => {
      if (this.state.currentMusicItem === musicItem) {
        this.playNext('next');
      }
      this.setState({
        musiclist: this.state.musiclist.filter(item => {
          return item !== musicItem
        })
      });

    })
    Pubsub.subscribe('PLAY_MUSIC', (msg, musicItem) => {
      this.playMusic(musicItem);
    })
    Pubsub.subscribe('IS_PLAY', (msg, isPlay) => {
      this.setState({
        isPlay: true
      })
    })
    Pubsub.subscribe('PLAY_PREV', (msg) => {
      this.playNext('prev');
    })
    Pubsub.subscribe('PLAY_NEXT', (msg) => {
      this.playNext('next');
    })

    Pubsub.subscribe('PLAY_MODE', (msg, clickTime) => {
      if (clickTime % 3 === 1) {
        $('#player').unbind($.jPlayer.event.ended);
        $('#player').bind($.jPlayer.event.ended, (e) => {
          this.playMusic(this.state.currentMusicItem);
        })

      } else if (clickTime % 3 === 2) {
        $('#player').unbind($.jPlayer.event.ended);
        $('#player').bind($.jPlayer.event.ended, (e) => {
          let index = Math.floor(Math.random() * this.state.musiclist.length);
          this.playMusic(this.state.musiclist[index]);
        })

      } else if (clickTime % 3 === 0) {
        $('#player').unbind($.jPlayer.event.ended);
        $('#player').bind($.jPlayer.event.ended, (e) => {
          this.playNext();
        })

      }
    })
  }

  componentWillUnmount() {
    Pubsub.unsubscribe('DELETE_MUSIC');
    Pubsub.unsubscribe('PLAY_MUSIC');
    Pubsub.unsubscribe('PLAY_PREV');
    Pubsub.unsubscribe('PLAY_NEXT');
    Pubsub.unsubscribe('IS_PLAY');
    Pubsub.unsubscribe('PLAY_MODE');
    $('#player').unbind($.jPlayer.event.ended);
  }

  render() {


    return (
      <div className="Music">
        <fieldset style={ { textAlign: 'center' } }>
          <legend>
            音乐播放器
          </legend>
        </fieldset>
        <div id="player"></div>
        <div className="row container-fluid">
          <div className="col-md-9">
            <Player {...this.state}/>
          </div>
          <div
               className="col-md-3 text-center"
               style={ { background: "#fff" } }>
            <Musiclist {...this.state} />
          </div>
        </div>
      </div>
    )
  }
}


export default Music;


