import React, { Component } from 'react';

class CommentList extends Component {
  render() {
    return (

      <tr className="comment-list">
        <td>
          { this.props.comment.author }
        </td>
        <td>
          { this.props.comment.message }
        </td>
      </tr>
      );
  }
}

export default CommentList;


