import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import LazyLoad from 'react-lazyload';
import './picture.scss';
let imageDatas = require('../data/imageDatas.json');

//利用自执行函数,将图片信息转成图片路径信息
imageDatas = (function genImageUrl(imageDatasArr) {
  for (let i = 0, j = imageDatasArr.length; i < j; i++) {
    let singleImageData = imageDatasArr[i];
    singleImageData.imageURL = require('../imgs/' + singleImageData.fileName);
    imageDatasArr[i] = singleImageData;
  }

  return imageDatasArr;
})(imageDatas);

class ImageFigure extends React.Component {

  /**
   * 点击处理函数
   */
  handleClick(e) {
    if (this.props.arrange.isCenter) {
      this.props.inverse();
    } else {
      this.props.center();
    }
    e.preventDefault();
    e.stopPropagation();

  }



  render() {
    let styleObj = {};
    //给styleObj添加元素的定位信息
    if (this.props.arrange.pos) {
      styleObj.left = this.props.arrange.pos.left;
      styleObj.top = this.props.arrange.pos.top;

    }
    //给styleObj添加元素的旋转角度信息
    if (this.props.arrange.rotate) {

      styleObj.transform = 'rotate(' + this.props.arrange.rotate + 'deg)';

    }


    if (this.props.arrange.isCenter) {
      styleObj.zIndex = 10;
    }


    //通过给figure元素添加class信息实现翻转
    let imgFigureClassName = 'img-figure';
    imgFigureClassName += this.props.arrange.isInverse ? ' is-inverse' : '';

    return (
      <figure
              className={ imgFigureClassName }
              style={ styleObj }
              onClick={ this.handleClick.bind(this) }>
        <figcaption>
          <LazyLoad height={ 220 }>
            <img
                 src={ this.props.data.imageURL }
                 alt={ this.props.data.title }
                 width="200px"
                 height="220px"></img>
          </LazyLoad>
          <h2 className="img-title">{ this.props.data.title }</h2>
          <div
               className="img-back"
               onClick={ this.handleClick.bind(this) }>
            <p className="desc">
              { this.props.data.desc }
            </p>
          </div>
        </figcaption>
      </figure>
      );
  }
}

class ControllerUnit extends React.Component {
  handleClick(e) {
    if (this.props.arrange.isCenter) {
      this.props.inverse();
    } else {
      this.props.center();
    }
    e.preventDefault();
    e.stopPropagation();
  }



  render() {
    let controllerUnitClassName = 'controller-unit';

    if (this.props.arrange.isCenter) {
      controllerUnitClassName += ' is-center';

      if (this.props.arrange.isInverse) {
        controllerUnitClassName += ' is-inverse';
      }
    }

    return (
      <span
            className={ controllerUnitClassName }
            onClick={ this.handleClick.bind(this) }></span>
      );
  }
}





class Picture extends Component {
  constructor(props) {
    super(props);

    this.state = {
      imgsArrangeArr: [
        /*{
          pos:{
            left:"",
            top:""
          },
          rotate:"",
          isInverse:false,
          isCenter: false

        }*/
      ]

    }
  }

  Constant = {
    centerPos: {
      left: 0,
      top: 0
    },
    hPosRange: { //水平取值范围
      leftSecX: [0, 0],
      rightSecX: [0, 0],
      y: [0, 0]
    },
    vPosRange: { //垂直方向的取值范围
      x: [0, 0],
      topY: [0, 0]
    }
  }


  componentDidMount() {

    let stageDOM = ReactDOM.findDOMNode(this.refs.stage),
      stageW = stageDOM.scrollWidth,
      stageH = stageDOM.scrollHeight,
      halfStageW = Math.ceil(stageW / 2),
      halfStageH = Math.ceil(stageH / 2);

    let imgFigureDOM = ReactDOM.findDOMNode(this.refs.imgFigure0),
      imgW = imgFigureDOM.scrollWidth,
      imgH = imgFigureDOM.scrollHeight,
      halfImgW = Math.ceil(imgW / 2),
      halfImgH = Math.ceil(imgH / 2);

    //计算中心图片的位置点
    this.Constant.centerPos = {
      left: halfStageW - halfImgW,
      top: halfStageH - halfImgH
    }
    //计算左侧,右侧图片排布位置的取值范围
    this.Constant.hPosRange.leftSecX[0] = -halfImgW;
    this.Constant.hPosRange.leftSecX[1] = halfStageW - halfImgW * 3;
    this.Constant.hPosRange.rightSecX[0] = halfStageW + halfImgW;
    this.Constant.hPosRange.rightSecX[1] = stageW - halfImgW;
    this.Constant.hPosRange.y[0] = -halfImgH;
    this.Constant.hPosRange.y[1] = stageH - halfImgH;

    //计算上侧图片排布位置的取值范围
    this.Constant.vPosRange.topY[0] = -halfImgH;
    this.Constant.vPosRange.topY[1] = halfStageH - halfImgH * 3;
    this.Constant.vPosRange.x[0] = halfStageW - imgW;
    this.Constant.vPosRange.x[1] = halfStageW;


    this.rearrange(0);
  }



  /*
   * 反转图片
   * @param index 当前被执行操作的索引
   * @return {Function} 闭包函数
   */
  inverse(index) {
    return (function() {
      let imgsArrangeArr = this.state.imgsArrangeArr;

      imgsArrangeArr[index].isInverse = !imgsArrangeArr[index].isInverse;

      this.setState({
        imgsArrangeArr: imgsArrangeArr
      });
    }.bind(this));

  }

  /**
   * 居中图片
   * @param index
   */
  center(index) {
    return (function() {
      this.rearrange(index);
    }.bind(this));
  }

  /*
   *获取区间内的一个随机值
   */
  getRangeRandom(low, high) {
    return Math.ceil(Math.random() * (high - low) + low);
  }

  /*
   *获取0-30度的随机角度值,可正可负
   */

  get30DegRandom() {
    let deg = '';
    deg = (Math.random() > 0.5) ? '+' : '-';
    return deg + Math.ceil(Math.random() * 30);
  }

  //重新布局所有图片
  rearrange(centerIndex) {

    let imgsArrangeArr = this.state.imgsArrangeArr;

    let Constant = this.Constant,
      centerPos = Constant.centerPos,
      hPosRange = Constant.hPosRange,
      vPosRange = Constant.vPosRange,
      hPosRangeLeftSecX = hPosRange.leftSecX,
      hPosRangeRightSecX = hPosRange.rightSecX,
      hPosRangeY = hPosRange.y,
      vPosRangeTopY = vPosRange.topY,
      vPosRangeX = vPosRange.x,
      imgsArrangeTopArr = [],
      topImgNum = Math.floor(Math.random() * 2),
      topImgSpliceIndex = 0;
    //切出需要居中的图片
    let imgsArrangeCenterArr = imgsArrangeArr.splice(centerIndex, 1);

    //居中centerIndex的图片
    imgsArrangeCenterArr[0] = {
      pos: centerPos,
      rotate: 0,
      isCenter: true
    };

    topImgSpliceIndex = Math.ceil(Math.random() * (imgsArrangeArr.length - topImgNum));
    //切出需要布局于上侧的图片
    imgsArrangeTopArr = imgsArrangeArr.splice(topImgSpliceIndex, topImgNum);


    //布局位于上侧的图片
    imgsArrangeTopArr.forEach((value, index) => {
      imgsArrangeTopArr[index] = {
        pos: {
          left: this.getRangeRandom(vPosRangeX[0], vPosRangeX[1]),
          top: this.getRangeRandom(vPosRangeTopY[0], vPosRangeTopY[1])
        },
        rotate: this.get30DegRandom(),
        isCenter: false

      }
    });
    //布局左右两侧的图片
    for (let i = 0, j = imgsArrangeArr.length, k = j / 2; i < j; i++) {
      let hPosRangeLORX = null;

      if (i < k) {
        hPosRangeLORX = hPosRangeLeftSecX;
      } else {
        hPosRangeLORX = hPosRangeRightSecX;
      }

      imgsArrangeArr[i] = {
        pos: {
          left: this.getRangeRandom(hPosRangeLORX[0], hPosRangeLORX[1]),
          top: this.getRangeRandom(hPosRangeY[0], hPosRangeY[1])
        },
        rotate: this.get30DegRandom(),
        isCenter: false
      }
    }


    if (imgsArrangeTopArr && imgsArrangeTopArr[0]) {
      imgsArrangeArr.splice(topImgSpliceIndex, 0, imgsArrangeTopArr[0]);
    }

    imgsArrangeArr.splice(centerIndex, 0, imgsArrangeCenterArr[0]);

    this.setState({
      imgsArrangeArr: imgsArrangeArr
    });


  }


  componentWillMount() {
    let imgsArrangeArr = [];
    for (let index = imageDatas.length - 1; index >= 0; index--) {
      if (!this.state.imgsArrangeArr[index]) {
        imgsArrangeArr[index] = {
          pos: {
            left: 0,
            top: 0,
          },
          rotate: 0,
          isInverse: false,
          isCenter: false,
        };


        this.setState({
          imgsArrangeArr: imgsArrangeArr
        });

      }
    }

  }

  render() {
    let imgFigures = [],
      controllerUnits = [];

    imageDatas.forEach((element, index) => {

      imgFigures.push(
        <ImageFigure
                     key={ index }
                     data={ element }
                     ref={ 'imgFigure' + index }
                     arrange={ this.state.imgsArrangeArr[index] }
                     inverse={ this.inverse(index) }
                     center={ this.center(index) } />
      );

      controllerUnits.push(
        <ControllerUnit
                        key={ index }
                        arrange={ this.state.imgsArrangeArr[index] }
                        inverse={ this.inverse(index) }
                        center={ this.center(index) } />

      );
    }, this);


    return (
      <div className="Picture">
        <fieldset>
          <legend>
            图片画廊
          </legend>
        </fieldset>
        <section
                 className="stage"
                 ref="stage">
          <section className="img-sec">
            { imgFigures }
          </section>
          <nav className="controller-nav">
            { controllerUnits }
          </nav>
        </section>
      </div>
      );
  }
}

export default Picture;



