import React, { Component } from 'react';
import './footer.scss';
class Footer extends Component {


  render() {
    return (
      <div className="Footer">
        <div className="container-fluid content">
          <div
               className="row"
               style={ { margin: '0 auto', height: '120px', lineHeight: '40px' } }>
            <div className="col-md-3 col-xs-6">
              <ul>
                <li>
                  <a href="https://github.com/"><img
                                                     src={ require('./footer-imgs/Git.png') }
                                                     alt="git"
                                                     width="26px" ></img>Git</a>
                </li>
                <li>
                  <a href="http://www.youku.com/"><img
                                                       src={ require('./footer-imgs/icon_youku.png') }
                                                       alt="youku"
                                                       width="26px" ></img>优酷</a>
                </li>
                <li>
                  <a href="https://weibo.com/"><img
                                                    src={ require('./footer-imgs/weibo.png') }
                                                    alt="weibo"
                                                    width="26px" ></img>微博</a>
                </li>
              </ul>
            </div>
            <div className="col-md-3 col-xs-6">
              <ul>
                <li>
                  <a href="https://www.zimuzu.tv/"><img
                                                        src={ require('./footer-imgs/meiju.png') }
                                                        alt="meiju"
                                                        width="26px" ></img>美剧</a>
                </li>
                <li>
                  <a href="https://btso.pw/tags"><img
                                                      src={ require('./footer-imgs/movie.png') }
                                                      alt="movie"
                                                      width="26px" ></img>电影</a>
                </li>
                <li>
                  <a href="https://y.qq.com/"><img
                                                   src={ require('./footer-imgs/music.png') }
                                                   alt="music"
                                                   width="26px" ></img>音乐</a>
                </li>
              </ul>
            </div>
            <div className="col-md-3 col-xs-6">
              <ul>
                <li>
                  <a href="https://reactjs.org/"><img
                                                      src={ require('./footer-imgs/react.png') }
                                                      alt="react"
                                                      width="26px" ></img>React</a>
                </li>
                <li>
                  <a href="https://cn.vuejs.org/"><img
                                                       src={ require('./footer-imgs/vue.png') }
                                                       alt="vue"
                                                       width="26px" ></img>Vue</a>
                </li>
                <li>
                  <a href="https://jquery.com/"><img
                                                     src={ require('./footer-imgs/jquery.png') }
                                                     alt="jquery"
                                                     width="26px" ></img>Jquery</a>
                </li>
              </ul>
            </div>
            <div className="col-md-3 col-xs-6">
              <ul>
                <li>
                  <a href="https://www.jd.com/"><img
                                                     src={ require('./footer-imgs/jingdong.png') }
                                                     alt="京东"
                                                     width="26px" ></img>京东</a>
                </li>
                <li>
                  <a href="https://www.tmall.com/"><img
                                                        src={ require('./footer-imgs/tianmao.png') }
                                                        alt="天猫"
                                                        width="26px" ></img>天猫</a>
                </li>
                <li>
                  <a href="https://www.amazon.cn/"><img
                                                        src={ require('./footer-imgs/amazon.png') }
                                                        alt="Amazon"
                                                        width="26px" ></img>亚马逊</a>
                </li>
              </ul>
            </div>
          </div>
          <p style={ { color: '#fff', marginTop: '20px' } }>
            © 2017
          </p>
        </div>
      </div>
      );
  }
}

export default Footer;


