import React , { Component } from "react";
import './lazyload.scss';
class Lazyload extends Component {
  render() {
    return (
      <div className="lazyload">
        <div className="panel panel-info">
          <div className="panel-heading">
            <h3 className="panel-title">关于图片懒加载问题</h3>
          </div>
          <div className="panel-body">
            <p>
              在用react框架写应用的时候,也不能避免的会有页面渲染的问题,当我们在一个页面需要加载大量图片的时候,如果不加缓冲想一次性就加载完成,对用户的体验会存在严重的问题。所以特意找了下图片懒加载的react插件,终于找到一个比较好的懒加载小插件react-lazyload。
            </p>
            <p>
              下面我们就来看看react-lazyload如何使用。
            </p>
            <p>
              第一步:安装插件
            </p>
            <pre>$ npm install --save react-lazyload</pre>
            <p>
              第二步:使用
            </p>
            <pre>import LazyLoad from 'react-lazyload';</pre>
            <pre>{ "<LazyLoad height={200}><img src='tiger.jpg' />" }</pre>
            <p>
              第三部:如果想要让组件默认是懒加载,按如下方法设置即可
            </p>
            <pre>import { 'lazyload' } from 'react-lazyload';</pre>
            <pre>@lazyload({ '{ height: 200,once: true,offset: 100 }' })</pre>
            <p>
              注意事项:LazyLoad组件要想达到理想的懒加载效果,请务必给其height属性赋值。
            </p>
          </div>
        </div>
      </div>

      );
  }
}

export default Lazyload;


