import React , { Component } from 'react';
import Pubsub from 'pubsub-js';
import $ from 'jquery';
import 'jplayer';

import Progress from './Progress';
import './player.scss';
/*总时间*/
let duration = 0;
/*当前进度*/
let current = 0;
/*播放状态*/
let isPlayNow = true;
let currentWidth = 0;
let clickTime = 0;

class Player extends Component {
  constructor(props) {
    super(props);
    this.state = {
      progress: 0,
      volume: 0,
      time: 0,
      isPlay: true,
      isPlayCycle: false
    };
  }

  componentWillMount() {
    if (this.props.isPlay) {
      isPlayNow = this.props.isPlay;
    }
    this.setState({
      time: current,
      isPlay: isPlayNow,
      progress: currentWidth
    });
  }

  componentDidMount() {
    $('#player').bind($.jPlayer.event.timeupdate, (e) => {
      /*总时间*/
      duration = e.jPlayer.status.duration;
      current = e.jPlayer.status.currentTime;
      currentWidth = e.jPlayer.status.currentPercentAbsolute;
      this.setState({
        volume: e.jPlayer.options.volume * 100,
        time: e.jPlayer.status.currentTime,
        progress: e.jPlayer.status.currentPercentAbsolute,
      })
    });
  }

  componentWillUnmount() {
    $('#player').unbind($.jPlayer.event.timeupdate);
    $('#player').unbind($.jPlayer.event.ended);
    isPlayNow = this.state.isPlay;
  }

  progressChangeHandler(progress) {
    $('#player').jPlayer(this.state.isPlay ? 'play' : 'pause', duration * progress);
  }

  changeVolumeHandler(progress) {
    $('#player').jPlayer('volume', progress);
  }


  play() {
    this.state.isPlay ? $('#player').jPlayer('pause') : $('#player').jPlayer('play');
    this.setState({
      isPlay: !this.state.isPlay
    })
  }
  playPrev() {
    Pubsub.publish('PLAY_PREV');
    this.setState({
      isPlay: true
    })
  }
  playNext() {
    Pubsub.publish('PLAY_NEXT');
    this.setState({
      isPlay: true
    })
  }

  playMode() {
    clickTime += 1;
    if (clickTime % 3 === 0) {
      $("#repeat").attr("class", "icon repeat-cycle");

    } else if (clickTime % 3 === 1) {
      $("#repeat").attr("class", "icon repeat-once");

    } else if (clickTime % 3 === 2) {
      $("#repeat").attr("class", "icon repeat-random");
    }

    Pubsub.publish('PLAY_MODE', clickTime);
  }



  render() {
    return (
      <div className="Player container-fluid">
        <div className='row'>
          <h3 className="list-link">我的私人音乐坊</h3>
        </div>
        <div className="music-section">
          <div className='row'>
            <div className="col-md-2">
            </div>
            <h3 className='col-md-4 song-title'>歌曲 - { this.props.currentMusicItem.title }</h3>
            <div className="col-md-6">
            </div>
          </div>
          <div className='row'>
            <div className="col-md-2"></div>
            <h4 className='col-md-4 song-artist'>演唱者 - { this.props.currentMusicItem.artist }</h4>
            <div className="col-md-6"></div>
          </div>
          <div className='row'>
            <div className={ `img-section center-block ${Math.ceil(this.state.time) >= Math.ceil(duration) ? 'pause' : this.state.isPlay ? '' : 'pause'}` }>
              <img
                   src={ this.props.currentMusicItem.cover }
                   alt={ this.props.currentMusicItem.title }
                   width="240px"
                   height="240px" />
            </div>
          </div>
          <div className="progress-change">
            <div className="row">
              <div className="col-md-7 col-xs-6"></div>
              <div className="col-md-3 col-xs-3">
                <div className="repeat-icon">
                  <i
                     className="icon repeat-cycle"
                     id="repeat"
                     onClick={ this.playMode.bind(this) }></i>
                </div>
              </div>
              <div className="col-md-2 col-xs-3">
                <p>
                  { (this.state.time / 60).toFixed(0) > 10 ? 10 : '0' + (this.state.time / 60).toFixed(0) } :
                  { ((this.state.time).toFixed(0) % 60 < 10) ? '0' + (this.state.time).toFixed(0) % 60 : (this.state.time).toFixed(0) % 60 } s
                </p>
              </div>
            </div>
          </div>
          <div className="the-progress">
            <Progress
                      progress={ this.state.progress }
                      barColor="#1E9FFF"
                      onProgressChange={ this.progressChangeHandler.bind(this) } />
          </div>
          <div className="row btn-section">
            <div className="col-md-3 song-volumn col-xs-2">
              <i className="icon-volume"></i>
            </div>
            <div className="col-md-3 col-xs-4">
              <div className="volumn-progress">
                <Progress
                          progress={ this.state.volume }
                          barColor="#5FB878"
                          onProgressChange={ this.changeVolumeHandler.bind(this) } />
              </div>
            </div>
            <div className='col-md-6 col-xs-6'>
              <div className="icon-play">
                <i
                   className="icon prev"
                   onClick={ this.playPrev.bind(this) }></i>
                <i
                   className={ `icon ${Math.ceil(this.state.time) >= Math.ceil(duration) ? 'play' : this.state.isPlay ? 'pause' : 'play'}` }
                   onClick={ this.play.bind(this) }></i>
                <i
                   className="icon next"
                   onClick={ this.playNext.bind(this) }></i>
              </div>
            </div>
          </div>
        </div>
      </div>


      );
  }
}

export default Player;


