import React, { Component } from 'react';
import LazyLoad from 'react-lazyload';
import './carousel.scss';

class Carousel extends Component {
  constructor(props) {
    super(props);

    this.state = {
      index: 0,
      speed: 5000,
      pageNum: this.props.imgDatas.length,
    }
  }

  intervalFun = () => {
    let index = this.state.index;

    this.setState({
      index: index + 1,
    });

    if (this.state.index > this.state.pageNum - 1) {
      this.setState({
        index: 0,
      });
    }

  }

  componentDidMount() {
    this.interval = setInterval(this.intervalFun, this.state.speed);
  }

  componentWillUnmount() {
    clearInterval(this.interval);
  }


  handleClick(index) {
    this.setState({
      index: index,
    });
    clearInterval(this.interval);
    this.interval = setInterval(this.intervalFun, this.state.speed);

  }

  handleLeft() {
    let index = this.state.index;

    if (index > 0) {
      this.setState({
        index: index - 1,
      });
    } else if (index === 0) {
      this.setState({
        index: this.state.pageNum - 1,
      });
    }
    clearInterval(this.interval);
    this.interval = setInterval(this.intervalFun, this.state.speed);
  }

  handleRight() {
    let index = this.state.index;
    if (index < this.state.pageNum - 1) {
      this.setState({
        index: index + 1,
      });
    } else if (index === this.state.pageNum - 1) {
      this.setState({
        index: 0,
      });
    }
    clearInterval(this.interval);
    this.interval = setInterval(this.intervalFun, this.state.speed);
  }


  render() {
    let imgFigure = [];
    let controllerUnits = [];

    let imgDatas = this.props.imgDatas;
    imgDatas.forEach((element, index) => {
      imgFigure.push(<div
                          key={ index }
                          className="every-img">
                       <LazyLoad height={ 600 }>
                         <img
                              src={ element.path }
                              alt={ element.alt }
                              width="800px"
                              height="600px" />
                       </LazyLoad>
                     </div>
      );
      controllerUnits.push(

        <span
              key={ index }
              className={ `controller-unit ${this.state.index === index?('active'):''}` }
              onClick={ this.handleClick.bind(this, index) }></span>


      );


    });

    return (
      <div className="Carousel">
        <fieldset>
          <legend>
            图片轮播
          </legend>
        </fieldset>
        <div className="stage-sec">
          <div
               className={ `img-sec ${'active'+this.state.index}` }
               style={ { "width": this.state.pageNum * 800 + "px" } }>
            { imgFigure }
          </div>
          <div className="units">
            { controllerUnits }
          </div>
          <div
               className="icon-left-sec"
               onClick={ this.handleLeft.bind(this) }>
            <img
                 className="icon-sec"
                 src={ require('./arrow.png') }
                 width="30px"
                 alt=""></img>
          </div>
          <div
               className="icon-right-sec"
               onClick={ this.handleRight.bind(this) }>
            <img
                 className="icon-sec"
                 src={ require('./arrow.png') }
                 width="30px"
                 alt=""></img>
          </div>
        </div>
      </div>
      );
  }
}

export default Carousel;


