import React, { Component } from 'react';
import ShowMovie from './movie/ShowMovie';
import Detail from './movie/Detail';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
class Movie extends Component {

  render() {

    return (

      <div className="Movie">
        <BrowserRouter>
          <Switch>
            <Route
                   path='/main/movie'
                   exact
                   component={ ShowMovie }></Route>
            <Route
                   path='/main/movie/:id'
                   component={ Detail }></Route>
          </Switch>
        </BrowserRouter>
      </div>
      );
  }
}

export default Movie;


