import React, { Component } from 'react';
import CommentList from './comment/CommentList';
import './comment.scss';

let commentDatas = [{
  author: "admin",
  message: "这是我们的留言版块,如有问题或建议请在此留言"
}]

class Comment extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [],
      value: ""
    }
  }

  componentWillMount() {
    this.setState({
      data: commentDatas
    });
  }


  handleClick() {
    let value = this.state.value;
    if (value !== "") {
      let newData = {
        author: "user",
        message: value
      };

      let data = this.state.data;

      data.push(newData);
      console.log(data);
      this.setState({
        data: data,
        value: ""
      });
    }
  }

  handlerChange(event) {
    let newValue = event.target.value;
    this.setState({
      value: newValue
    });
  }

  render() {
    let data = this.state.data;
    let dataFigure = data.map((element, index) => {
      return (
        <CommentList
                     key={ index }
                     comment={ element } />);
    });

    return (
      <div className="comment">
        <fieldset>
          <legend>
            留言板模拟
          </legend>
        </fieldset>
        <div className="container msg-sec">
          <h4>留言信息展示</h4>
          <table className="table">
            <tbody>
              { dataFigure }
            </tbody>
          </table>
        </div>
        <div className="container leave-msg-sec">
          <h4><label htmlFor="name"> 留言区域 </label></h4>
          <textarea
                    className="form-control"
                    rows="5"
                    value={ this.state.value }
                    onChange={ this.handlerChange.bind(this) } />
          <button
                  className="btn btn-default"
                  onClick={ this.handleClick.bind(this) }>
            提交
          </button>
        </div>
      </div>
      );
  }
}

export default Comment;


