import React, { Component } from 'react';
import './pagebtn.scss';

class PageButton extends Component {
  constructor(props) {
    super(props);

    this.state = {
      index: 0,
      pageNum: this.props.options.currentPage,
    }
  }

  setUp() {
    if (this.state.index > 0) {
      this.setState({
        index: this.state.index - 1,
        pageNum: this.state.pageNum - 1,
      }, function() {
        this.props.pageChange(this.state.index);
      });
    }
  }

  setDown() {
    let index = this.state.index;
    let totalPage = this.props.options.totalPage;

    if (index + 1 < totalPage) {
      this.setState({
        index: index + 1,
        pageNum: this.state.pageNum + 1,
      }, function() {
        this.props.pageChange(this.state.index);
      });
    }
  }

  handleClick(index) {
    this.setState({
      index: index,
      pageNum: index + 1,
    }, function() {
      this.props.pageChange(this.state.index);
    });
  }

  render() {

    let total = this.props.options.totalPage;
    let spanFigure = [];
    for (var i = 0; i < total; i++) {
      spanFigure.push(<span
                            key={ i }
                            className={ `btn ${this.state.pageNum === i+1 ? "btn-primary" : ""}` }
                            onClick={ this.handleClick.bind(this, i) }>{ i + 1 }</span>);
    }

    return (
      <div className="PageButton">
        <span
              onClick={ this.setUp.bind(this) }
              className="btn btn-primary">上一页</span>
        { spanFigure }
        <span className="btn btn-primary">{ this.state.index + 1 }页/{ this.props.options.totalPage }页</span>
        <span
              onClick={ this.setDown.bind(this) }
              className="btn btn-primary">下一页</span>
      </div>
      );
  }
}

export default PageButton;


