import React, { Component } from 'react';
import Carousel from './carousel/Carousel';
import Pagination from './page/Pagination';
import { imgDatas } from '../data/imgDatas';

class Main extends Component {


  render() {
    return (
      <div className="main">
        <Carousel imgDatas={ imgDatas } />
        <Pagination/>
      </div>
      );
  }
}

export default Main;


