import React from 'react';
import Pubsub from 'pubsub-js';
import './musiclistitem.scss';

class MusicListItem extends React.Component {
  playMusic(musicItem) {
    Pubsub.publish('PLAY_MUSIC', musicItem);
    Pubsub.publish('IS_PLAY', true);
  }
  deleteMusic(musicItem, event) {
    event.stopPropagation();
    Pubsub.publish('DELETE_MUSIC', musicItem);
  }
  render() {
    let musicItem = this.props.musicItem;
    return (
      <tr
          onClick={ this.playMusic.bind(this, musicItem) }
          className={ `${this.props.focus ? 'focus':''}` }>
        <td>
          <p className='title'>
            <strong>{ musicItem.title }</strong> -
            { musicItem.artist }
          </p>
        </td>
        <td>
          <p
             onClick={ this.deleteMusic.bind(this, musicItem) }
             className='delete'>
            <img
                 src={ require('./imgs/alter_delete.png') }
                 width="20px"
                 height="20px"
                 alt=""></img>
          </p>
        </td>
      </tr>
    )
  }
}

export default MusicListItem;


