import React , { Component } from 'react';
import { MOVIE_LIST } from './movielist';
import { Link } from 'react-router-dom';
import './detail.scss';
import { Player } from 'video-react';

class Detail extends Component {

  render() {

    let list = MOVIE_LIST;

    let id = this.props.match.params.id;
    let data = list.filter((element) => {
      return element.id + "" === id
    });

    return (
      <div className="Detail">
        <fieldset>
          <legend>
            详情页
          </legend>
        </fieldset>
        <div className="movie-info text-center">
          <p>
            { data[0].name } --
            { data[0].time } --
            { data[0].director }
          </p>
          <div className="container movie-box">
            <div className="player center-block">
              <Player
                      playsInline
                      poster=""
                      src={ data[0].src } />
            </div>
          </div>
          <Link
                to="/movie"
                className="btn btn-success center-block movie-link">
            回到电影主页
          </Link>
        </div>
      </div>);
  }
}


export default Detail;


